html {
  scroll-behavior: smooth;
}

body {
  color: #2887d9;
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  color: #3a3b3d;
  text-decoration: none;
}

.visually-hidden {
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  border: 0 !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  position: absolute !important;
}

li {
  list-style-type: none;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 390px) {
  .container {
    width: 390px;
  }
}

@media screen and (min-width: 1366px) {
  .container {
    width: 1366px;
  }
}

@media screen and (min-width: 1920px) {
  .container {
    width: 1920px;
  }
}

@media screen and (max-width: 1355px) {
  .logo-div {
    justify-content: space-between;
    align-items: center;
    padding-bottom: 13px;
    display: flex;
  }
}

.burger-button, .search, .login-language button {
  background-color: #0000;
  border: none;
}

.logo img {
  width: 186px;
  height: 48px;
}

svg {
  fill: #0000;
}

.login-language {
  border-top: 1px solid #72b7f2;
  border-bottom: 1px solid #72b7f2;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.login-language .cabinet:before, .login-language .global:before {
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
  margin-right: 12px;
  display: inline-block;
}

.login-language .cabinet:before {
  background-image: url("vhіd-v-kabіnet.145bdfce.svg");
}

.login-language .global:before {
  background-image: url("global.0d813efe.svg");
}

.login-language button {
  text-align: center;
  color: #3a3b3d;
  justify-content: center;
  font-family: Open Sans, sans-serif;
  font-size: 16px;
  display: flex;
}

.login-language .eng {
  color: #d0e1f1;
  font-family: Open Sans, sans-serif;
  font-size: 16px;
}

.nav-list {
  margin-bottom: 80px;
}

.nav-item {
  border-bottom: 1px solid #c2c8d6;
  font-family: Open Sans, sans-serif;
  font-size: 16px;
  line-height: 44px;
}

.nav-item:not(:last-child) {
  margin-bottom: 8px;
}

.address {
  margin-bottom: 16px;
}

.address .item:before {
  content: "";
  background-position: center;
  background-repeat: no-repeat;
  width: 28px;
  height: 28px;
  display: inline-block;
}

.address .map:before {
  background-image: url("map.fc7b8a1e.svg");
}

.address .sms:before {
  background-image: url("sms.997615c2.svg");
}

.address .callcalling:before {
  background-image: url("callcalling.8c105a91.svg");
}

.address .item p {
  color: #2887d9;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.address address {
  color: #3a3b3d;
  font-family: Open Sans, sans-serif;
  font-size: 16px;
  line-height: 22px;
}

.social-networks {
  justify-content: center;
  align-items: center;
  display: flex;
}

.social-networks .item:not(:first-child) {
  margin-left: 36px;
}

